<template>
  <div>
    <p>
      あなた<br>
      HP: {{playerHP}}<br>
      MP: {{playerMP}}
    </p>

    <p>
      デスピサロ<br>
      HP: {{psaroHP}}<br>
      MP: {{psaroMP}}
    </p>

    <button class="button is-danger" @click="attack" :disabled="end">攻撃</button>


    <b-dropdown>
      <button class="button is-link" slot="trigger" :disabled="end"><span>呪文</span></button>
      <b-dropdown-item @click="blazeMost">メラゾーマ</b-dropdown-item>
      <b-dropdown-item @click="thordain">ギガデイン</b-dropdown-item>
    </b-dropdown>

    <b-dropdown>
      <button class="button is-success" slot="trigger" :disabled="end"><span>道具</span></button>
      <b-dropdown-item @click="chimeraWing">キメラのつばさ</b-dropdown-item>
    </b-dropdown>

    <p>{{message}}</p>
  </div>
</template>

<script>
    export default {
        name: "DragonQuest4",
        data() {
            return {
                playerHP: 1000,
                playerMP: 100,
                psaroHP: 1000,
                psaroMP: 100,
                message: "デスピサロがあらわれた．",
                end: false
            }
        },
        methods: {
            attack() {
                this.psaroHP = this.psaroHP - 100;
                this.psaroTurn()
            },
            blazeMost() {
                this.psaroHP = this.psaroHP - 180;
                this.playerMP = this.playerMP - 20;
                this.psaroTurn()
            },
            thordain() {
                this.psaroHP = this.psaroHP - 230;
                this.playerMP = this.playerMP - 30;
                this.psaroTurn()
            },
            chimeraWing() {
                this.end = true
            },
            psaroTurn() {
                this.playerHP = this.playerHP - 200
            }
        }
    }
</script>

